* {
  padding: 0;
  margin: 0;

  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  font-size: 1rem;
  text-decoration: none;
  outline: none;
  color: black;

  border-radius: 0;
  box-shadow: none;

  -ms-overflow-style: none;
  scrollbar-width: none;
}

*::-webkit-scrollbar {
  display: none;
}
